@media only screen and (min-width: 280px) and (max-width: 768px) {
  .banner {
    display: block;

    & > *:last-child { padding: 0; }
  }

  .secondary_banner {
    margin-bottom: 5vh;
    h2 { margin-bottom: 1vh;}
  }

  .banner__title {
    justify-content: flex-start;
    mask-image: url('../assets/images/Untitled-2.png');

    & > * {
      width: 85vw;
      translate: 0 0;
      transform: translateX(-50%);
      text-align: center;
    }
  
    h1 { width: 60vw; }
  
    li {
      margin-top: 5vh;
      padding: 0;
  
      &:last-child {
        margin-top: 1vh;
        padding-top: 1vh;
        border-left: none;
        border-top: 1px solid $grey;
      }
    }
  }

  .banner__title_lg {
    height: 95vh;
    padding-top: 15vh;
}
  
  .banner__title_sm {
    height: auto;

    & > h2 { padding: 10vh 0; }
  }

  .banner__title__alt {
    h2 { padding-bottom: 0; }
    span { margin-bottom: 8vh; }
  }

  .contacts {
    flex-direction: column;
    gap: 15px;
    transform: translateY(-25vh);
  
    & > a {
      color: #FFF;
      span:first-child { display: none; }
    }

    svg {
      path { fill: #FFF; }
      .path_no_fill { fill: $theme; }
    }
  }

  .view {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5vh;
  }

  .secondary_view {
    menu {
      height: auto;
      margin: 0;
      li { width: calc(100% / 2); }
    }
  }

  .view__content {
    width: 85vw;
    padding: 0;
    h3 { margin: 0; }
  }

  .view__content_alt {
    width: 85vw;
    margin-top: 0;
  }

  .flex_row { gap: 20px; }

  .flex_row_alt {
    span:first-child { width: 100%; }
    gap: 10px;
  }

  #section_about {
    article {
      div {
        background-image: $background_image;
        & > * { text-align: left; }
        h3 { text-decoration: none; }
      }
    }
  }

  #section_projects {
    & > div {  
      margin: 0;
      flex-direction: column;
    }

    article { width: 100%; }

    & > button:last-child { margin: 3vh 0 0; }
  }

  .jump_to_project_section {
    width: 85vw;
    margin: 0 0 5vh;
    gap: 5vh;
    left: 50%;
    transform: translateX(-50%);
    
    h2 {
      &:first-child {
        em { margin-bottom: 2vh; }
        &:hover { em { left: 0; } }
      }
      &:last-child {
        em { margin-top: 2vh; }
        &:hover { em { left: 0; } }
      }
    }
  
    em { display: block; }
  }
  
  .toast_text {
    translate: 0 0;
    bottom: 15vh;
    transform: translateX(-50%);
  }

  .section_about__list {
    line-height: $line_height;
    padding-right: 0;
  }
}